body {
  background: #042d57;
  font-family: sans-serif;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.main-section {
  background: transparent;
  max-width: 600px;
  width: 90%;
  height: 500px;
  margin: 30px auto;
}

.clock-holder {
  width: 100%;
  background: #fff;
  margin: 30px auto;
  position: relative;
  border-radius: 15px;
}

.stopwatch {
  padding: 30px 0px;
  text-align: center;
}
.stopwatch span {
  color: #042d57;
  display: inline-block;

  font-size: 22px;
  padding: 20px;
  border: 2px solid #042d57;
  border-radius: 15px;
  width: 70px;
}

.stopwatch .stopwatch-btn {
  background: #fff;
  margin-top: 30px;
  margin-left: 5px;
  border: none;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  width: 150px;
  transition: background 0.5s;
  cursor: pointer;
}
.stopwatch .stopwatch-btn-red {
  border: 2px solid #e92222;
  color: #e92222;
}
.stopwatch .stopwatch-btn-yel {
  border: 2px solid #ffc900;
  color: #ffc900;
}

.stopwatch .stopwatch-btn-blu {
  border: 2px solid #042d57;
  color: #042d57;
}
.stopwatch .stopwatch-btn-red:hover {
  background: #e92222;
  border: 2px solid #e92222;
  color: #fff;
}
.stopwatch .stopwatch-btn-yel:hover {
  background: #ffc900;
  border: 2px solid #ffc900;
  color: #fff;
}
.stopwatch .stopwatch-btn-blu:hover {
  background: #042d57;
  border: 2px solid #042d57;
  color: #fff;
}

.link-project {
  text-align: center;
  padding: 20px 0;
}

.link-project a {
  font-size: 16px;
  font-weight: 400;
  color: #042d57;
  text-decoration: none;
}

.app-title {
  font-size: 35px;

  font-weight: 700;
  color: #042d57;
  text-align: center;
  padding-top: 20px;
}
